import { useEffect, useState } from "react";
import { commonArgs, blogItemTypes } from "../configs/types";
import { List, Typography, Grid } from "@mui/material";
import { langs } from "../configs/site";
import { blogData } from "../configs/blogdata";
export function BlogItem({ blog, lang, theme }: blogItemTypes) {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const targetElement = document.getElementById(`blog-item-${blog.slug}`);
    if (!targetElement) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.unobserve(entry.target);
        }
      },
      { rootMargin: "0px", threshold: 0.1 }
    );

    observer.observe(targetElement);

    return () => observer.disconnect();
  }, [blog.slug]);

  return (
    <a href={blog.slug} className="block w-full">
      <div id={`blog-item-${blog.slug}`} className="shadow-lg rounded-sm p-6">
        <div className="h-48 w-72">
          {isVisible && (
            <img
              src={blog.image}
              alt={blog[lang as keyof typeof langs].title}
              className="rounded-lg h-full w-full object-cover"
            />
          )}
        </div>
        <div className="flex justify-between mt-4">
          <Typography variant="h6">
            {blog[lang as keyof typeof langs].title}
          </Typography>
          <Typography variant="subtitle1">{blog.updated_at}</Typography>
        </div>
        <Typography variant="body1" className="mt-2">
          {blog[lang as keyof typeof langs].description}
        </Typography>
        <div className="mt-2">
          <List className="flex text-xs text-wrap flex-auto space-x-1">
            {blog.tags.map((tag, index) => (
              <li className="m-x-1" key={index}>
                {"#"}
                {tag}
              </li>
            ))}
          </List>
        </div>
      </div>
    </a>
  );
}

export function Blog({ lang, theme }: commonArgs) {
  return (
    <div className="m-6">
      <Grid container spacing={4} justifyContent="center">
        {blogData.map((item, index) => (
          <Grid
            item
            xs={12}
            sm={6}
            md={4}
            lg={3}
            key={index}
            className="grid-flow-dense"
          >
            <BlogItem blog={item} lang={lang} theme={theme} />
          </Grid>
        ))}
      </Grid>
    </div>
  );
}
